import * as THREE from 'three'

import Experience from './Experience.js'
import {JERSEY_DATA} from "./Utils/Utils";

export default class Jersey
{
	constructor()
	{
		this.experience = new Experience()
		this.resources = this.experience.resources
		this.scene = this.experience.scene

		this.setModel();
	}

	setModel() {
		this.model = {}
		this.model.mesh = this.resources.items.jersey.children[2];//Socks
		this.model.mesh = this.resources.items.jersey.children[1];//Pant
		this.model.mesh = this.resources.items.jersey.children[0];//Shirt
		this.model.textureColor = this.resources.items.manutdColor;
		this.model.textureNormal = this.resources.items.manutdNormal;
		this.model.mesh.scale.set(0.02, 0.02, 0.02);
		this.model.mesh.position.y = -4.5;
		this.scene.add(this.model.mesh);
		//
		this.model.mesh.material = new THREE.MeshStandardMaterial({
			map: this.model.textureColor,
			normalMap: this.model.textureNormal,
			side: THREE.DoubleSide
		});

		let collar08 = this.resources.items.collar08.children[0];//Collar
		collar08.scale.set(0.02, 0.02, 0.02);
		collar08.position.y = -4.5;
		this.scene.add(collar08);
		//
		collar08.material = new THREE.MeshStandardMaterial({
			map: this.model.textureColor,
			normalMap: this.model.textureNormal,
			side: THREE.DoubleSide
		});
		this.model.collar = collar08;
		// const data = [];
		// const dataArr = JERSEY_DATA.split('\n');
		// for(let i = 0; i<dataArr.length; i++) {
		// 	const nums = dataArr[i].split(' ');
		// 	if(nums.length < 0) {
		// 		console.error("Error nums: " + nums);
		// 	}
		// 	data.push({
		// 		pos: [nums[0], nums[1], 0],
		// 		uv: [nums[2], nums[3]]
		// 	});
		// }
		// const posAttr = [];
		// const normalAttr = [];
		// const uvAttr = [];
		// for(let i = 0; i<data.length; i++) {
		// 	const row = data[i];
		// 	posAttr.push(...row.pos.map(h=>h * 2 - 1));
		// 	normalAttr.push(0, 0, 1);
		// 	uvAttr.push(...row.uv.map(h=>parseFloat(h)));
		// }
		// const geometry = new THREE.BufferGeometry();
		// geometry.setAttribute("position", new THREE.Float32BufferAttribute(new Float32Array(posAttr),3));
		// geometry.setAttribute("normal", new THREE.Float32BufferAttribute(new Float32Array(normalAttr),3));
		// geometry.setAttribute("uv", new THREE.Float32BufferAttribute(new Float32Array(uvAttr),2));
		//
		// const material2 = new THREE.MeshBasicMaterial( );
		//
		// this.model.mesh = new THREE.Mesh(geometry, material2);


		const directionalLight = new THREE.DirectionalLight( 0xffffff, 2 );
		directionalLight.position.z = 4;
		directionalLight.position.y = 4;
		directionalLight.target = this.model.mesh;
		this.scene.add( directionalLight );

		const directionalLight2 = new THREE.DirectionalLight( 0xffffff, 2 );
		directionalLight2.position.z = -4;
		directionalLight2.position.y = -4;
		directionalLight2.target = this.model.mesh;
		this.scene.add( directionalLight2 );

		const directionalLight3 = new THREE.DirectionalLight( 0xffffff, 2 );
		directionalLight3.position.x = -4;
		directionalLight3.target = this.model.mesh;
		this.scene.add( directionalLight3 );

		const directionalLight4 = new THREE.DirectionalLight( 0xffffff, 2 );
		directionalLight4.position.x = 4;
		directionalLight4.target = this.model.mesh;
		this.scene.add( directionalLight4 );

		// for(let i = 0; i<this.model.mesh.children.length; i++) {
		// 	this.model.mesh.children[i].material = new THREE.MeshBasicMaterial({
		// 		color: 0xffffff
		// 	});
		// }
	}

	changeTexture(textureUrl) {
		new THREE.TextureLoader().load(
			textureUrl,
			texture => {
				this.model.mesh.material.map = texture;
				this.model.mesh.material.needsUpdate = true;

				this.model.collar.material.map = texture;
				this.model.collar.material.needsUpdate = true;
			},
			xhr => {
				console.log((xhr.loaded / xhr.total) * 100 + "% loaded");
			},
			error => {
				console.log("An error happened" + error);
			}
		);
	}
}
